import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-hint';

const InputHint = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

InputHint.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

InputHint.displayName = 'InputHint';

export default InputHint;
