import React, { Fragment, useRef } from 'react';
import { bool, node, number, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import Fieldset from '../fieldset';
import Legend from '../legend';
import SelectGroupContainer from '../select-group-container';
import SelectGroupMessage from '../select-group-message';
import Icon from '../icon';
import shortid from 'shortid';

const CSS_PREFIX = 'vds-select-group';
const CSS_STATE = 'vds-state';

const SelectGroup = ({
  children,
  className,
  id = shortid.generate(),
  disabled,
  errorIcon,
  errorText,
  helpIcon,
  helpText,
  invalid,
  legend,
  showErrorText,
  showHelpText,
  ...remainingProps
}) => {
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  return (
    <div
      aria-describedby={showErrorText || showHelpText ? `select-group-message-${uid}` : undefined}
      className={classnames(
        CSS_PREFIX,
        {
          [`${CSS_STATE}--disabled`]: Boolean(disabled),
          [`${CSS_STATE}--error`]: Boolean(invalid)
        },
        className
      )}
      {...remainingProps}
    >
      <Fieldset>
        <Legend>{legend}</Legend>
        <SelectGroupContainer>
          {children}
          <SelectGroupMessage
            id={`select-group-message-${uid}`}
            show={(showHelpText && Boolean(helpText)) || (showErrorText && Boolean(errorText))}
            invalid={invalid}
          >
            {!invalid && showHelpText && Boolean(helpText) ? (
              <Fragment>
                {helpIcon} {helpText}
              </Fragment>
            ) : (
              undefined
            )}
            {invalid && showErrorText && Boolean(errorText) ? (
              <Fragment>
                {errorIcon} {errorText}
              </Fragment>
            ) : (
              undefined
            )}
          </SelectGroupMessage>
        </SelectGroupContainer>
      </Fieldset>
    </div>
  );
};

SelectGroup.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   * Input Message Icon
   */
  errorIcon: node,

  /**
   * Error Text
   */
  errorText: node,

  /**
   * Input Help Message Icon
   */
  helpIcon: node,

  /**
   * Input Help Message
   */
  helpText: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Legend
   */
  legend: node,

  /**
   * Select Group Message
   */
  selectGroupMessage: node,

  /**
   * Select Group Message Icon
   */
  selectGroupMessageIcon: node,

  /**
   * Show Error Message
   */
  showErrorText: bool,

  /**
   * Show Help Message
   */
  showHelpText: bool
};

SelectGroup.defaultProps = {
  errorIcon: <Icon name="error" resolution="low" />
};

SelectGroup.displayName = 'SelectGroup';

export default SelectGroup;
