import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-option';

const InputOption = ({ children, className, ...remainingProps }) => (
  <span
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

InputOption.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

InputOption.displayName = 'InputOption';

export default InputOption;
