import React from 'react';
import { bool, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-select-group-message';
const CSS_STATE = 'vds-state';

const SelectGroupMessage = ({
  ariaAtomic,
  ariaLive,
  children,
  className,
  invalid,
  role,
  show,
  ...remainingProps
}) => (
  <span
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: !show,
      [`${CSS_STATE}--error`]: Boolean(invalid)
    }, className)}
    role={role}
    {...remainingProps}
  >
    {children}
  </span>
);

SelectGroupMessage.propTypes = {
  /**
   * Aria Atomic
   */
  ariaAtomic: oneOf(['true', 'false']),

  /**
   * Aria Live
   */
  ariaLive: oneOf(['assertive', 'polite', 'off']),

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Role
   */
  role: string,

  /**
   * Show
   */
  show: bool
};

SelectGroupMessage.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'assertive',
  role: 'alert'
};

SelectGroupMessage.displayName = 'SelectGroupMessage';

export default SelectGroupMessage;
