import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Select from '../../select';
import SelectGroup from '../../select-group';
import SelectGroupDelimiter from '../../select-group-delimiter';
import Typography from '../../typography';

const SelectExample = () => {
  const [stateOne, setStateOne] = useState({
    value: ''
  });

  const [stateTwo, setStateTwo] = useState({
    bank: '',
    card: ''
  });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Select</Typography>
          <Typography>Select takes all native React props directly, (e.g. onChange, etc)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Select Card"
          >
            <option value="" />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select with preselection</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Select Card"
            value="signature_card"
          >
            <option value="" />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select with invalid state.</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            errorText="Invalid Card Selection"
            invalid={stateOne.value === ''}
            label="Select Card"
            onChange={e => setStateOne({ value : e.target.value })}
            showErrorText={stateOne.value === ''}
          >
            <option value="" />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select Group with validity states</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <SelectGroup
            invalid={stateTwo.bank === '' &&  stateTwo.card === ''}
            legend="Credit Card"
            errorText="Please fill out the Credit Card info field."
            showErrorText
          >
            <Select
              ariaLabel="Bank Name"
              placeholder="Bank Name"
              errorText="Invalid Bank"
              invalid={stateTwo.bank === '' || (stateTwo.bank === '' &&  stateTwo.card === '')}
              onChange={e => setStateTwo({ ...stateTwo, bank : e.target.value })}
              showErrorText={stateTwo.bank === '' && !(stateTwo.bank === '' &&  stateTwo.card === '')}
            >
              <option value="" />
              <option value="BOA">Bank of America</option>
              <option value="CHA">Chase</option>
              <option value="WFO">Wells Fargo</option>
            </Select>
            <SelectGroupDelimiter> - </SelectGroupDelimiter>
            <Select
              ariaLabel="Card Type"
              placeholder="Card Type"
              errorText="Invalid Card Type"
              invalid={stateTwo.card === '' || (stateTwo.bank === '' &&  stateTwo.card === '')}
              onChange={e => setStateTwo({ ...stateTwo, card : e.target.value })}
              showErrorText={stateTwo.card === '' && !(stateTwo.bank === '' &&  stateTwo.card === '')}
            >
              <option value="" />
              <option value="VISA">Visa</option>
              <option value="MC">Mastercard</option>
              <option value="AMEX">American Express</option>
              <option value="DIS">Discover</option>
            </Select>
          </SelectGroup>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select (ReadOnly)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            label="Select Card"
            readOnly
            value="signature_card"
          >
            <option value="" />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Select (Disabled)</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Select
            disabled
            label="Select Card"
            value="signature_card"
          >
            <option value="" />
            <option value="silver_card">Silver Signature Card</option>
            <option value="signature_card">Visa Signature Card</option>
            <option value="platinum_card">Platinum Visa Card</option>
            <option value="premium_card">Premium Card With An Unnecessarily Long Description</option>
          </Select>
        </Col>
      </Row>
    </Grid>
  );
};

export default SelectExample;
