import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-select-group-delimiter';

const SelectGroupDelimiter = ({ ariaHidden, children, className, ...remainingProps }) => (
  <span
    aria-hidden={ariaHidden}
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

SelectGroupDelimiter.propTypes = {
  /**
   * Aria hidden
   */
  ariaHidden: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SelectGroupDelimiter.defaultProps = {
  ariaHidden: 'true'
};

SelectGroupDelimiter.displayName = 'SelectGroupDelimiter';

export default SelectGroupDelimiter;
